async function connectcode()
{
  var acounts=null;
    const Web3 = require("web3");
    var error=false;
    var web3=null;
    
    var errorMessage="";
        if (window.ethereum) {

          try{  
             
          acounts=await window.ethereum.request({ method: 'eth_requestAccounts' });
         
         
          try {
           await window.ethereum.request({
             method: 'wallet_switchEthereumChain',
             params: [{ chainId: '0x61'}],
           });
           
         } catch (switchError) {
           
           if (switchError.code === 4902) {
             try {
               await window.ethereum.request({
                 method: 'wallet_addEthereumChain',
                 params: [{ chainId: '0x61', rpcUrls: ['https://data-seed-prebsc-1-s1.binance.org:8545/'],chainName:"bsctestnet" }],
               });
               
             } catch (addError) {
                
               error=true;
               errorMessage=addError.message;
             }
           }
           else{
           error=true;
           errorMessage=switchError.message;
          
           }
          }
         web3 = new Web3(window.ethereum);

        }catch(requestError)
        {
           // console.log(requestError)
           error=true; 
           errorMessage=requestError.message;
         
        }
         
        }
        else{
            error=true;
            errorMessage="noprovider"
           
        }
        

        if(error==false)
        {
            web3 = new Web3(window.ethereum);
            
          

        }
        var finalAcc=null;
        if(acounts!=null)
        finalAcc=acounts[0];
       
return{
    "error":error,
    "web3":web3,
    "errorMessage":errorMessage,
    "acounts":finalAcc
}
}
export default connectcode;