import styles from './Graph3.module.css';
import ReactApexChart from 'react-apexcharts'
import { useEffect, useState } from 'react';
import { getCandleChartData, getPriceChartData, setPredictionIndex } from "../utils/contractData.js"
import { useSelector, useDispatch} from "react-redux";

function Graph3() {
const [refinedData, setRefinedData] = useState([]);
const currenctCurrency=useSelector(state=>state.currentCurrency);
const currenctGroup=useSelector(state=>state.currentGroup);
const tokens = useSelector(state => state.tokens);
const chartData=useSelector(state=>state.chartData)
var a=tokens;

  const [lineData, setLineData] = useState([]);
  async function f1() {
   
      getPriceChartData(1, "minutely").then((res) => {
        var mylinedata = [];
        for (var i = 0; i < res.prices.length; i++) {
          if (i % 30 == 0)
            mylinedata.push({ x: new Date(res.prices[i][0]), y: Math.round(res.prices[i][1] * 10000) / 10000 });
        }
        setLineData(mylinedata)
      })

      getCandleChartData().then((res) => {
        var myData = [];
        res.map((a) => {
          myData.push({
            x: new Date(a[0]),
            y: [a[1], a[2], a[3], a[4]]

          })

        })
        setRefinedData(myData)
      })
   
  }

  async function f2() {
  
      getPriceChartData(7, "hourly").then((res) => {
        var mylinedata = [];
        for (var i = 0; i < res.prices.length; i++) {
          if(i%4==0)
          mylinedata.push({ x: new Date(res.prices[i][0]), y: Math.round(res.prices[i][1] * 10000) / 10000 });
        }
        setLineData(mylinedata)
      })

      getCandleChartData("7").then((res) => {
        console.log("7day")
        console.log(res)
        var myData = [];
        res.map((a, index) => {
            myData.push({
              x: new Date(a[0]),
              y: [a[1], a[2], a[3], a[4]]

            })

        })
        setRefinedData(myData)
      })
  }

  async function f3() {
   
      getPriceChartData(30, "daily").then((res) => {
        console.log(res);
        var mylinedata = [];
        for (var i = 0; i < res.prices.length; i++) {
         
          mylinedata.push({ x: new Date(res.prices[i][0]), y: Math.round(res.prices[i][1] * 10000) / 10000 });
        }
        setLineData(mylinedata)
      })

      getCandleChartData("30").then((res) => {
        //console.log("7day")
        //console.log(res)
        var myData = [];
        res.map((a, index) => {
          if(index%6==0)
            myData.push({
              x: new Date(a[0]),
              y: [a[1], a[2], a[3], a[4]]

            })

        })
        setRefinedData(myData)
      })

  }

  async function f4() {
    
      getPriceChartData(360, "daily").then((res) => {
        console.log(res);
        var mylinedata = [];
        for (var i = 0; i < res.prices.length; i++) {
         if(i%4==0)
          mylinedata.push({ x: new Date(res.prices[i][0]), y: Math.round(res.prices[i][1] * 10000) / 10000 });
        }
        setLineData(mylinedata)
      })

      getCandleChartData("365").then((res) => {
        //console.log("7day")
        //console.log(res)
        var myData = [];
        res.map((a, index) => {
          
            myData.push({
              x: new Date(a[0]),
              y: [a[1], a[2], a[3], a[4]]

            })

        })
        setRefinedData(myData)
      })
   
  }



  var options = {
    chart: {


      type: 'line',
      height: 500
    },
    legend: {
        itemMargin: {
            horizontal: 30,
            vertical: 30
        },

    },

    plotOptions: {
      candlestick: {
        colors: {
          upward: '#A0E3B2',
          downward: '#FFA700'
        }
      },

    },

    stroke: {
      width: 3
    },
    title: {
      text: a[currenctGroup]+"/USD",
      align: 'left'
    },
    xaxis: {
      type: 'datetime'
    },
    yaxis: {
      tooltip: {
        enabled: true
      }
    }
  }
  useEffect(() => {
if(chartData!=0)
    f1();
    
  }, [currenctGroup,currenctCurrency,chartData])


  return (
    <>
      <div className={styles.menu}>
        <button className={styles.btn} onClick={() => {
          f1();

        }}>30m</button>
        <button className={styles.btn} onClick={() => {
          f2();

        }}>4H</button>
        <button className={styles.btn} onClick={() => {
          f3();

        }}>1D</button>
        <button className={styles.btn} onClick={() => {
          f4();

        }}>4D</button>
      </div>
      <div className={styles.App}>
        <ReactApexChart options={options} series={[{ data: refinedData, type: "candlestick", name: "Candle Chart", color: "#A0E3B2" }, { data: lineData, type: "line", name: "Line Chart", color: "#FFA700" }]} type="candlestick" height={350} />

      </div>
    </>
  );
}

export default Graph3;
